<template>
    <div>
        <div class="tf-idf" v-if="state.data">
            <div class="tf-idf-buttons">
                    <button v-for="(words, i) in state.data" :key="i"
                            :class="{'active': state.current == i}" 
                            @click="state.current = i">Group {{ parseInt(i) + 1 }}</button>
                </div>
            <div style="width: 500px;height:500px;">
                    <WordCloud :words="createWordcloudWords(state.data[state.current])" :colors="colors[state.current]" />
            </div>
        </div>
        <div style="margin-top: 2rem;"><small><i>Note: Colors are randomly chosen from a pool of party colors in a partition</i></small></div>
   </div>
</template>

<script lang="ts">
import { getData } from '@/services/data.service'
import { defineComponent, onMounted, reactive } from 'vue'
import d3Cloud from 'd3-cloud' 
import { group } from 'node_modules/@types/d3'

import WordCloud from '@/components/d3/WordCloud.vue'


type TfIdfWord = {
    word: string;
    tf_idf: number;
}
interface TfIdf {
    [key: string]: TfIdfWord[];
}

type range = [number, number]

const colors = [
    ['#e6801a', '#3fb2be', '#e07ea8', '#733280', '#98b42b','#4cff4c'],
    ['#eac73e'],
    ['#254264'],
    ['#a82721'],
    ['var(--color-1)'],
    ['var(--color-1)']
]

function scaleValue(value: number, from: range, to: range) {
	var scale = (to[1] - to[0]) / (from[1] - from[0]);
	var capped = Math.min(from[1], Math.max(from[0], value)) - from[0];
	return ~~(capped * scale + to[0]);
}

const createWordcloudWords = (words: TfIdfWord[]): d3Cloud.Word[] => {
    const range: range = [Infinity, -Infinity] 
    words.forEach(word => {        
        if (word.tf_idf < range[0]) {
            range[0] = word.tf_idf
        } else if (word.tf_idf > range[1]) {
            range[1] = word.tf_idf
        }
    })
    return words.map(word => {
        return {
            text: word.word,
            size: scaleValue(word.tf_idf, range, [20,100])
        }
    })
}

export default defineComponent({
    components: { WordCloud },
    setup() {
        const state = reactive({
            data: null,
            current: 0,
            wordclouds: {}
        }) as {
            data: TfIdf | null;
            current: number;
            wordclouds: {[key: string]: d3Cloud.Word[]}
        }

        onMounted(async () => {
            state.data = await getData<TfIdf>('data/tfidf/groups.json')
        })

        return { state, colors, createWordcloudWords }
    }
})
</script>

<style lang="scss" scoped>
.tf-idf {
    display: flex;
    &-buttons {
        display: flex;
        justify-content: center;
        flex-direction: column;
        margin-right: 2rem;
    }
}
button {
    margin-bottom: 1rem;
}
</style>
