
import { defineComponent, onMounted, onUnmounted, PropType, reactive, ref } from 'vue'

import * as d3 from 'd3'
import { d3Types } from '@/components/d3/types'
import { tooltip } from '@/services/tooltip.service'
import { getData } from '@/services/data.service'
import { getPersonImgUrl } from '@/services/person.service'

/*const scale = d3.scaleOrdinal(d3.schemeCategory10);
const color = (node: d3Types.d3Node) => {
    return scale(node.group ? node.group.toString() : '1')
}*/
const colors: {[group: string]: string} = {
    'S': '#a82721',
    'V': '#254264',
    'O': '#eac73e',
    'DF': '#eac73e',
    'RV': '#733280',
    'SF': '#e07ea8',
    'EL': '#e6801a',
    'KF': '#96b226',
    'NB': '#127b7f',
    'LA': '#3fb2be',
    'KD': '#8b8474',
    'ALT': '#00FF00',
    'CD': '#a70787',
    'IA': '#ff0000',
    'UFG': '#4d4d4d'
}
const color = (node: d3Types.d3Node) => {
    if (node.img) {
        return 'url(#img' + node.id + ')'
    }
    return colors[node.group] || node.group || 'steelblue'
}
const image = (node: d3Types.d3Node) => {
    return getPersonImgUrl(node.name)
}
const radius = (node: d3Types.d3Node) => {
    return node.size || node.degree && Math.log(node.degree) * 2.5 || 8
}

type GraphNode<N> = d3.SimulationNodeDatum & N
type GraphLink<N,L> = d3.SimulationLinkDatum<GraphNode<N>> & L
interface Graph<N,L> {
    simulation: d3.Simulation<GraphNode<N>,GraphLink<N,L>> | null;
    nodes: GraphNode<N>[];
    links: GraphLink<N,L>[];
}

export default defineComponent({
  props: {
        src: {
            type: String,
            required: true
        },
        height: { default: 1000 },
        width: { default: 1000 },
        minDegree: Number
  },
  setup(props) {
    const svg = ref<SVGSVGElement | null>(null)
    const graph = reactive<Graph<d3Types.d3Node, d3Types.d3Link>>({
        simulation: null,
        nodes: [],
        links: []
    })

    onMounted(async () => {      
        const data = await getData<d3Types.d3Graph>(props.src)        
        
        setTimeout(() => {
            createGraph(data)
        }, 750)
    })

    onUnmounted(() => {
        graph.simulation?.stop()
    })

    function createGraph (data: d3Types.d3Graph) {
        const nodes = data.nodes.map(d => Object.create(d)).filter(d => props.minDegree ? d.degree > props.minDegree : true)
        const nodeIds = nodes.map(d => d.id)               
        const links = data.links.map(d => Object.create(d)).filter(d => nodeIds.includes(d.source) && nodeIds.includes(d.target))        

        graph.simulation = d3.forceSimulation(nodes)
            .force("collide", d3.forceCollide().radius((d: any) => 1 + radius(d)))
            .force("link", d3.forceLink(links)
                .distance(d => 0)
                .id((d: any) => d.id))
            .force("charge", d3.forceManyBody().strength(-75))
            .force("center", d3.forceCenter(props.width / 2, props.height / 2));
                
        setTimeout(() => {
            d3.select(svg.value)
                .selectAll<SVGCircleElement, unknown>('circle')
                .call(d3.drag<SVGCircleElement, unknown>()
                    .on("start", onDragStart)
                    .on("drag", onDrag)
                    .on("end", onDragEnd))
        }, 1000)

        let showLinks = false
        graph.simulation.on("tick", () => {
            graph.nodes.splice(0)
            graph.nodes.push(...nodes)
            showLinks && graph.links.splice(0)
            showLinks && graph.links.push(...links.slice(0,2000))
        });

        setTimeout(() => {
            showLinks = true
        }, 4000)
    }

    let draggedNode: GraphNode<d3Types.d3Node> | undefined | null = null 

    function onDragStart (event: any) { 
        if (!event.active) graph.simulation?.alphaTarget(0.8).restart();
        graph.simulation?.velocityDecay(0.2)
        const draggedIndex = graph.simulation?.find(event.subject.x, event.subject.y)?.index        
        draggedNode = draggedIndex != undefined ? graph.simulation?.nodes()[draggedIndex]: null
    }
    function onDrag (event: any) {
        if (draggedNode) {
            draggedNode.fx = event.x
            draggedNode.fy = event.y 
        }     
        tooltip().hide()
    }
    function onDragEnd (event: any) {
        if (!event.active) graph.simulation?.alphaTarget(0);
        if (draggedNode) {
            draggedNode.fx = null
            draggedNode.fy = null
        }
        tooltip().show()
    }

    return { svg, graph, radius, color, image }
  },
})
