
import { defineComponent, onMounted, onUnmounted, PropType, reactive, ref } from 'vue'

import * as d3 from 'd3'


const fills = ['url(#group0)','#eac73e','#254264','#a82721','var(--color-1)','var(--color-1)']

const margin = {top: 30, right: 20, bottom: 40, left: 50}

export default defineComponent({
  props: {
        height: { default: 300 },
        width: { default: 600 },
        bins: { default: 10 },
        color: { default: 'var(--color-1)' },
        title: { default: 'Title' },
        xlabel: { default: 'X-label' },
        ylabel: { default: 'Y-label' },
        ylog: { default: false }
  },
  setup(props) {
    const svg = ref<SVGSVGElement | null>(null)
    const current = ref('sentiment')
    const update = ref<(c: string) => void>(() => ({}))

    onMounted(async () => {
        const data = await d3.csv<any>("data/group_lix_sentiment.csv")
        update.value = createChart(data)
        update.value('sentiment')
    })

    function createChart (data: d3.DSVRowArray<any>) {
        const width = props.width - margin.left - margin.right
        const height = props.height - margin.top - margin.bottom

        const SVG = d3.select(svg.value)

        // Initialize the X axis
        const x = d3.scaleBand()
            .range([ margin.left, width ])
            .padding(0.2);
        const xAxis = SVG.append("g")
            .attr('class', 'xAxis')
            .attr("transform", "translate(0," + height + ")")

        // Initialize the Y axis
        const y = d3.scaleLinear()
            .range([ height, 0]);
        const yAxis = SVG.append("g")
            .attr("transform", "translate("+margin.left+",0)")

        return (c: string) => {    
            current.value = c        

            // X axis
            x.domain(data.map((d: any) => d.group))
            xAxis.transition().duration(1000).call(d3.axisBottom(x))

            // Add Y axis
            y.domain([0, d3.max(data, (d: any) => +d[c]) as number]);
            yAxis.transition().duration(1000).call(d3.axisLeft(y));

            // variable u: map data to existing bars
            var u = SVG.selectAll<SVGRectElement, unknown>("rect")
            .data(data)

            u
            .enter()
            .append("rect")
            .merge(u)
            .transition()
            .duration(1000)
                .attr("x", (d:any): number => x(d.group) as number)
                .attr("y", (d: any) => y(d[c]))
                .attr("width", x.bandwidth())
                .attr("height", (d:any) => height - y(d[c]))
                .attr("fill", (d) => fills[d.group as any])
        } 
    }



    return { svg, update, current }
  },
})
