<template>
    <svg
        class="d3-cloud"
        ref="svg"
        :width="'100%' || width"
        :height="'100%' || height"
        :viewBox="`0, 0, ${width}, ${height}`"
    >
    </svg>
</template>

<script lang="ts">
import { defineComponent, onMounted, PropType, ref, watch } from 'vue'

import * as d3 from 'd3'
import d3Cloud from 'd3-cloud' 
import { getData } from '@/services/data.service';

const getRandom = (arr: any[]) => {  
    return arr[Math.floor(Math.random() * arr.length)]
}

export default defineComponent({
  props: {
        src: String,
        words: Object as PropType<d3Cloud.Word[]>,
        height: { default: 1000 },
        width: { default: 1000 },
        colors: { default: ['var(--color-1)']}
  },
  setup(props) {
    const svg = ref<SVGSVGElement | null>(null)

    const cloud = d3Cloud()
                    .size([props.width, props.height])
                    .padding(5)
                    .rotate(function() { return ~~(Math.random() * 2) * 90; })
                    .font("Impact")
                    .fontSize((d: any) => d.size)
                    .start()
                    .on("end", draw);

    onMounted(async () => {
      const words = props.src ? await getData<d3Cloud.Word[]>(props.src) : props.words || []       
      cloud.words(words)
      cloud.start()
    })

    function draw(words: d3Cloud.Word[]) {
      d3.select(svg.value)
      .append("g")
        .attr("transform", "translate(" + cloud.size()[0] / 2 + "," + cloud.size()[1] / 2 + ")")
      .selectAll("text")
        .data(words)
      .enter().append("text")
        .style('fill', d => getRandom(props.colors))
        .style('fill-opacity', d => Math.random() * 0.5 + 0.5)
        .style("font-size", d => d.size + "px")
        .style("font-family", cloud.font())
        .attr("text-anchor", "middle")
        .attr("transform", d => "translate(" + [d.x, d.y] + ")rotate(" + d.rotate + ")")
        .text((d: any) => d.text);
    }

    watch(() => props.words, (words) => {
      cloud.stop()
      d3.select(svg.value).selectChild('g').remove()
      words && cloud.words(words)
      words && cloud.start()
    })
    
    return { svg }
  },
})
</script>

<style lang="scss" scoped>
.d3-cloud {

}
</style>